import React from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";

export default function MyAppBar({
  portfolio,
  customers,
  experience,
  contact,
}) {
  const scrollToPortfolio = () =>
    portfolio.current.scrollIntoView({ behavior: "smooth", block: "start" });
  const scrollToCustomers = () =>
    customers.current.scrollIntoView({ behavior: "smooth", block: "start" });
  const scrollToExperience = () =>
    experience.current.scrollIntoView({ behavior: "smooth", block: "start" });
  const scrollToContact = () =>
    contact.current.scrollIntoView({ behavior: "smooth", block: "start" });

  return (
    <AppBar position="static" className="appbar">
      <Toolbar>
        <Typography variant="h6" className="appbar-title">
          Erick Llerenas
        </Typography>
        <Button color="primary" onClick={scrollToPortfolio}>
          Portfolio
        </Button>

        <Button color="primary" onClick={scrollToExperience}>
          Experience
        </Button>
        <Button color="primary">Skills</Button>
        <Button color="primary" onClick={scrollToCustomers}>
          Customers
        </Button>

        <Button color="primary">Values</Button>
        <Button color="primary" onClick={scrollToContact}>
          Contact
        </Button>
      </Toolbar>
    </AppBar>
  );
}
